<template>
  <yd-layout>
    <yd-navbar slot="navbar" title="我的已办">
      <a href="javascript:;" @click="goBack" slot="left">
        <yd-navbar-back-icon></yd-navbar-back-icon>
      </a>
    </yd-navbar>
      <yd-pullrefresh :callback="loadList" ref="pullrefreshList_">
      	<yd-list theme="4" v-if="list && list.length>0">
	      	<rx-list-item 
	      		v-for="item in list" 
	      		:key="item.pkId" 
	      		:href="{name:'getInstInfo',params:{instId:item.actInstId,type:'act'}}" 
	      		type='link'
      		>
	          
	          	<div class="message-img">
								<span>{{getFirstLetter(item.subject)}}</span>
							</div>
		        	<div class="item-content">
		        		
			          <div slot="title" class="item-title">
			          	<span><span class="condition"></span>{{item.subject}}</span>
			          	<span>{{item.createTime}}</span>
			          </div>
					      
		        	</div>
	        </rx-list-item>
	      </yd-list>
       </yd-pullrefresh>

		<rx-nodata :amount="list.length"></rx-nodata>

    <yd-tabbar slot="tabbar" activeColor="#3399ff">
    	<yd-tabbar-item title="工作台" :link="{name:'index'}">
        <yd-icon name="workspace" slot="icon"></yd-icon>
      </yd-tabbar-item>
      <yd-tabbar-item title="我的待办" :link="{name:'myToDo'}">
        <yd-icon name="my-tolist" slot="icon"></yd-icon>
        <yd-badge slot="badge" type="danger" v-if="total>0">{{displayCount(total)}}</yd-badge>
      </yd-tabbar-item>
      <yd-tabbar-item title="我的已办"  link="#" active>
        <yd-icon name="already-flow" slot="icon"></yd-icon>
        <!--<yd-badge slot="badge" type="danger" v-if="total>0">{{displayCount(total)}}</yd-badge>-->
      </yd-tabbar-item>
    </yd-tabbar>
  </yd-layout>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue'
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import listItem from '@/components/common/ListItem';
import router from '@/router/index';

Vue.component(listItem.name, listItem);

export default {
  data(){
    return {
      list:[],
		  page:0,
		  total:0
    }
  },
  created() {
  	this.loadList();
  },
  methods:{
    loadList(){
    	var url=_baseUrl +"/mobile/bpm/myAttendsData.do";
			var params="pageIndex="+this.page+"&pageSize=10";
			loadRefreshList(url,params,this,data=>{
				this.total=data.total;
			},true);
			
    },
 
		getFirstLetter(word){
			return word.substr(0,1).toUpperCase();
		}
  }
}
</script>
<style type="text/css" scoped>

	.list-title{
		font-weight: normal;
	}
	
	
</style>


